<template>
	<view class="waterfall-flow" ref="waterfall">
		<!-- class="item" 必须有, 限制好 item 宽度  -->
		<view class="item" :style="item.style" v-for="(item, index) in newList" :key="item.key" :data-index="index" @click="choose">
			<!-- 盒子样式，自定义 -->
			
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			list: {
				type: Array,
				default() {
					return []
				}
			}
		},
		data () {
			return {
				newList: [], // 兼容小程序
				boxHeight: []
			}
		},
		watch: {
			list: function (newVal, oldVal) {
				if (newVal != oldVal) {
					// #ifndef MP-WEIXIN
					this.newList = this.list;
					// #endif
					// #ifdef MP-WEIXIN
					this.newList = this.newList.concat(newVal)
					// #endif
					let mark = oldVal.length;
					let len = this.list.length;
					let screenWidth = uni.getSystemInfoSync().screenWidth;
					let style = '';
					this.$nextTick(async function () {
						const query = uni.createSelectorQuery().in(this);
						let dataArray = [];
						await new Promise((resolve, reject)=>{
							setTimeout(() => {
								query.selectAll('.waterfall-flow .item').fields({size: true}, data => {
									dataArray = data;
									resolve()
								}).exec();
							}, 1020) // 针对图片 mode="widthFix" 尺寸改变时的延迟
						})
						for (let i = mark; i < len; i++) {
							if (i < 2) {
								style = `top: 0; left: ${(screenWidth/2) * i}px;`
								this.boxHeight.push(dataArray[i].height)
							} else {
								let minHeight = this.boxHeight[0];
								if (minHeight > this.boxHeight[1]) {
									minHeight = this.boxHeight[1];
									this.boxHeight[1] = minHeight + dataArray[i].height;
									style = `top: ${minHeight}px; left: ${(screenWidth/2)}px;`
								} else {
									this.boxHeight[0] = minHeight + dataArray[i].height;
									style = `top: ${minHeight}px; left: 0;`
								}
							}
							this.$set(this.newList[i], 'style', style);
							this.$forceUpdate();
						}
					})
				}
			}
		},
		methods: {
			// 选中
			choose(e) {
				let index = e.currentTarget.dataset.index;
				this.$emit('click', this.newList[index]);
			}
		}
	}
</script>

<style scoped lang="scss">
	image {
		display: block;
		will-change: transform;
	}
	.waterfall-flow {
		position: relative;
		background-color: #F4F4F4;
		
	}
	/* 为了初始化，还为定位上，加上这个 */
	.item {
		width: 375rpx;
		position: absolute;
		top: -375rpx;
		left: -375rpx;
	}
	.box {
		// margin: 15rpx;
		background: #FFFFFF;
		box-shadow: 0px 8rpx 27rpx 0px rgba(0, 0, 0, 0.06);
		border-radius: 18rpx;
		overflow: hidden;
		
		
		.pic{
			position:relative;
			
			.cover{
				width: 337rpx;
				height:auto;
			}
			
			.play{
				position:absolute;
				right:23rpx;
				top:20rpx;
				width: 33rpx;
				height: 33rpx;
				z-index:99;
			}
		}
		
		
		.content{
			padding:15rpx 22rpx 15rpx;
			
			.location{
				display:flex;
				
				text{
					font-size: 22rpx;
					font-weight: 500;
					color: rgb(141,141,141);
				}
				
				image{
					margin-right:8rpx;
					width: 29rpx;
					height: 29rpx;
				}
			}
			
			.title{
				padding:20rpx 0 15rpx;
				font-size: 26rpx;
				font-weight: 500;
				color: #333333;
			}
			
			.bottom{
				.user{
					display:flex;
					align-items: center;
					
					image{
						width: 38rpx;
						height: 38rpx;
						box-shadow: 0px 0px 12rpx 0px rgba(62, 62, 62, 0.11);
						border-radius: 50%;
						margin-right:13rpx;
					}
					
					text{
						font-size: 20rpx;
						font-weight: bold;
						color: #8D8D8D;
					}
				}
				
				
			}
			
			
			
		}
		
		
	}
</style>
